<template>
    <div class="container">
        <h1>Notas</h1>

        <b-alert
        :show="dismissCountDown"
        dismissible
        :variant="mensaje.color"
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
        >
            {{mensaje.texto}}
        </b-alert>

        <!-- Formulario para editar notas -->
        <form class="my-4" @submit.prevent="editarNota(notaEditar)" v-if="editar">
            <h5 class="text-warning">EDITAR NOTA</h5>
            <input type="text" class="form-control my-2" placeholder="Nombre" v-model="notaEditar.nombre">
            <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="notaEditar.descripcion">
            <b-button type="submit" class="btn-warning my-2 btn-block">Guardar</b-button>
            <b-button type="submit" class="btn-danger my-2 btn-block" @click="editar = false">Cancelar</b-button>
        </form>

        <!-- Formulario para agregar notas -->
        <form class="my-4" @submit.prevent="agregarNota()" v-if="!editar">
            <h5 class="text-success">AGREGAR NUEVA NOTA</h5>
            <input type="text" class="form-control my-2" placeholder="Nombre" v-model="nota.nombre">
            <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="nota.descripcion">
            <b-button type="submit" class="btn-success my-2 btn-block">Agregar</b-button>
        </form>

        <table class="table my-4">
            <thead>
                <tr>
                <th scope="col">ID</th>
                <th scope="col">Nombre</th>
                <th scope="col">Descripcion</th>
                <th scope="col">Acciones</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in notas" :key="index">
                <th scope="row">{{item._id}}</th>
                <td>{{item.nombre}}</td>
                <td>{{item.descripcion}}</td>
                <td>
                    <!-- <b-button class="btn-warning" @click="alerta()">Accion</b-button> -->
                    <b-button class="btn-danger btn-sm mx-1 my-1" @click="eliminarNota(item._id)"><i class="material-icons">delete</i></b-button>
                    <b-button class="btn-warning btn-sm mx-1 my-1" @click="activarEdicion(item._id)"><i class="material-icons">edit</i></b-button>
                </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
    name: 'notas',
    data() {
        return {
            notas: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {color: '', texto: ''},
            nota: {nombre: '', descripcion: ''},
            editar: false,
            notaEditar: {}
        };
    },
    created(){
        this.listarNotas();
    },
    methods: {
        alerta(){
            this.mensaje.color = 'success';
            this.mensaje.texto = 'Probando alerta';
            console.log(this.mensaje);
            this.showAlert();
        },
        listarNotas(){
            this.axios.get('/nota')
            .then(res => {
                console.log(res.data);
                this.notas = res.data;
            })
            .catch(e => {
                console.log(e.response);
            })
        },
        agregarNota(){
            this.axios.post('/nueva-nota', this.nota)
            .then(res => {
                this.notas.push(res.data);
                this.nota.nombre = '';
                this.nota.descripcion = '';
                this.mensaje.color = 'success';
                this.mensaje.texto = 'Nota agregada';
                this.showAlert();
            })
            .catch(e => {
                console.log(e.response);
                if(e.response.data.error.errors.nombre.message){
                    this.mensaje.texto = e.response.data.error.errors.nombre.message;
                }else{
                    this.mensaje.texto = 'Error de sistema'
                }
                this.mensaje.color = 'danger';
                this.showAlert();
            })
            console.log(this.nota);
        },
        eliminarNota(id){
            console.log(id);
            this.axios.delete(`/nota/${id}`)
            .then(res => {
                const index = this.notas.findIndex(item => item._id === res.data._id);
                this.notas.splice(index, 1);
                this.mensaje.color = 'warning';
                this.mensaje.texto = 'Nota Eliminada';
                this.showAlert();
            })
            .catch(e => {
                console.log(e.response);

            });
        },
        editarNota(item){
            this.axios.put(`/nota/${item._id}`, item)
            .then(res => {
                const index = this.notas.findIndex(n => n._id === res.data._id);
                this.notas[index].nombre = res.data.nombre;
                this.notas[index].descripcion = res.data.descripcion;
                this.mensaje.color = 'success';
                this.mensaje.texto = 'Se guardaron los cambios';
                this.showAlert();
                this.editar = false;
            })
            .catch(e => {
                console.log(e.response);
            })
        },
        activarEdicion(id){
            console.log(id);
            this.editar = true;
            this.axios.get(`/nota/${id}`)
            .then(res => {
                this.notaEditar = res.data;
            })
            .catch(e => {
                console.log(e.response);
            })
            
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }
    }
}
</script>